body {
  font-family: "Courier New", Courier, monospace;
  color: rgb(0, 0, 0);
  width: auto;
  background-color: rgb(232, 232, 232);
}
.container {
  background-color: rgb(232, 232, 232);
  padding: 10px;
  text-align: center;
  align-content: center;
  margin: auto;
}
img {
  border-radius: 5px;
  margin: 10px;
  width: 250px;
  object-fit: contain;
}
.number {
  position: absolute;
  padding: 5px;
  margin-left: -10px;
  margin-top: -10px;
  color: brown;
  background-color: rgb(250, 238, 194);
}
.movielist {
  width: 300px;
  position: inherit;
  padding: 10px;
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
  box-shadow: 1px 1px 5px rgb(197, 197, 197);
  margin: auto;
  margin-top: 40px;
}
#searchbar {
  /* position: fixed; */
  z-index: +1;
  /* display: block; */
  /* width: 60%; */
  padding: 10px;
  background-color: rgba(252, 252, 252, 0);
  backdrop-filter: blur(6px);
}
button {
  color: rgb(0, 0, 0);
  padding: 4px;
  background-color: rgb(255, 221, 221);
  border: 0.5px solid rgb(227, 227, 227);
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  border-radius: 5px;
}
#title {
  /* display: absolute; */
  /* position: fixed; */
  text-align: center;
  margin: auto;
}
